"use client"

import { Card } from "@/components/ui/card"
import { DataTable } from "@/components/ui/data-table"
import { ArrowUpDown } from "lucide-react"
import { Button } from "@/components/ui/button"
import { ColumnDef } from "@tanstack/react-table"

// Generate more realistic sample data
const generateSampleData = () => {
  const teams = [
    "ML Research",
    "Data Science",
    "Computer Vision",
    "NLP Team",
    "Recommendation Systems",
    "Deep Learning",
    "AI Infrastructure",
    "Analytics",
    "Model Training",
    "MLOps"
  ]
  
  const gpuTypes = ["NVIDIA A100", "NVIDIA V100", "NVIDIA A6000", "NVIDIA DGX"]
  const statuses = ["Active", "Completed", "Pending", "Suspended"]
  
  return Array.from({ length: 50 }, (_, i) => ({
    id: i + 1,
    team: teams[Math.floor(Math.random() * teams.length)],
    gpuType: gpuTypes[Math.floor(Math.random() * gpuTypes.length)],
    usageHours: +(Math.random() * 300).toFixed(1),
    costPerHour: +[1.8, 2.5, 3.0, 4.0][Math.floor(Math.random() * 4)],
    totalCost: 0, // Will be calculated
    billingPeriod: ["Dec 2023", "Nov 2023", "Oct 2023"][Math.floor(Math.random() * 3)],
    status: statuses[Math.floor(Math.random() * statuses.length)],
  })).map(item => ({
    ...item,
    totalCost: +(item.usageHours * item.costPerHour).toFixed(2)
  }))
}

const usageData = generateSampleData()

type Usage = {
  id: number
  team: string
  gpuType: string
  usageHours: number
  costPerHour: number
  totalCost: number
  billingPeriod: string
  status: string
}

const columns: ColumnDef<Usage>[] = [
  {
    accessorKey: "team",
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Team
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      )
    },
    filterFn: "equals"
  },
  {
    accessorKey: "gpuType",
    header: "GPU Type",
  },
  {
    accessorKey: "usageHours",
    header: ({ column }) => {
      return (
        <div className="text-right">
          <Button
            variant="ghost"
            onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
          >
            Usage (Hours)
            <ArrowUpDown className="ml-2 h-4 w-4" />
          </Button>
        </div>
      )
    },
    cell: ({ row }) => {
      const hours = parseFloat(row.getValue("usageHours"))
      return <div className="text-right">{hours.toFixed(1)}</div>
    },
  },
  {
    accessorKey: "costPerHour",
    header: ({ column }) => (
      <div className="text-right">Cost/Hour ($)</div>
    ),
    cell: ({ row }) => {
      const cost = parseFloat(row.getValue("costPerHour"))
      return <div className="text-right">${cost.toFixed(2)}</div>
    },
  },
  {
    accessorKey: "totalCost",
    header: ({ column }) => {
      return (
        <div className="text-right">
          <Button
            variant="ghost"
            onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
          >
            Total Cost ($)
            <ArrowUpDown className="ml-2 h-4 w-4" />
          </Button>
        </div>
      )
    },
    cell: ({ row }) => {
      const amount = parseFloat(row.getValue("totalCost"))
      return <div className="text-right font-medium">${amount.toFixed(2)}</div>
    },
  },
  {
    accessorKey: "billingPeriod",
    header: "Billing Period",
    filterFn: "equals"
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ row }) => {
      const status = row.getValue("status") as string
      const colorMap: { [key: string]: string } = {
        Active: "bg-green-100 text-green-800",
        Completed: "bg-blue-100 text-blue-800",
        Pending: "bg-yellow-100 text-yellow-800",
        Suspended: "bg-red-100 text-red-800"
      }
      return (
        <span className={`inline-flex items-center rounded-md px-2 py-1 text-xs font-medium ${colorMap[status]}`}>
          {status}
        </span>
      )
    },
    filterFn: "equals"
  },
]

export default function UsagePage() {
  return (
    <div className="space-y-8">
      <div>
        <h2 className="text-3xl font-bold tracking-tight">Usage</h2>
        <p className="text-muted-foreground mt-2">
          Track GPU usage and costs across teams
        </p>
      </div>

      <Card className="p-6">
        <DataTable columns={columns} data={usageData} />
      </Card>
    </div>
  )
}
